.breadcrumbs {
  @include flex-v-center;
  margin-bottom: $lg-offset;

  &__item:not(:last-child) {
    // margin-right: 8px;

    &::after {
      content: '|';
      margin: 0 $xs-offset;
    }
  }

  &__link {
    position: relative;
    z-index: 2;
    font-weight: 500;
    font-size: 14px;
    color: $dark-color;

    &::before {
      content: '';
      position: absolute;
      top: -4px;
      left: -7px;
      z-index: -1;
      // border: 1.5px solid $accent-color;
      width: calc(100% + 14px);
      height: calc(100% + 8px);
      background-color: rgba($dark-color, 0.8);
      opacity: 0;
      visibility: hidden;
      transition: opacity .3s ease-in-out, visibility .3s ease-in-out, background-color .3s ease-in-out;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      color: $light-color;
      &::before {
        opacity: 1;
        visibility: visible;
      }
    }

    &:active {
      color: $dark-color;

      &::before{
        opacity: 1;
        visibility: visible;
        background-color: $accent-color;
      }
    }
  }
}
.breadcrumbs__link.focus-visible {
  &::before {
    opacity: 1;
    visibility: visible;
  }
}
.current-link {
  color: rgba($dark-color, 0.5);
}
