.product-table {
  &__wrapper {
    margin-bottom: $lg-offset;
    color: $dark-color;
    background-color: $light-color;
  }

  // &__title {
  //   margin: 0;
  //   margin-bottom: $xs-offset;
  //   font-weight: 600;
  //   font-size: 20px;
  //   color: inherit;
  // }
  &__table {
    padding: 30px;
    border-collapse: collapse;
    min-width: 100%;

    caption {
      margin: 0;
      margin-bottom: $xs-offset;
      font-weight: 600;
      font-size: 20px;
      color: inherit;
    }

    thead {
      tr {
        background-color: rgba($dark-color, 0.9);
      }
      th {
        font-weight: 500;
        font-size: 20px;
        color: $light-color;
      }
    }

    tr {
      display: grid;
      grid-template-columns: repeat(8, 1fr);
      border: 1px solid #ddd;
      padding: 20px;
      width: 100%;

      th, td {
        grid-column: 1 span;
        text-align: start;

        &:first-child {
          grid-column: 3 span;
        }
      }
      td {
        font-weight: 400;
        font-size: 18px;
        line-height: 144%;
        color: $dark-color;
      }
    }
  }
}


