.product {
  @include flex-all-center;

  flex-direction: column;
  margin: 0 auto;
  color: $dark-color;
  background-color: $light-color;

  &__container {
    padding-top: 172px;
  }
}
.product-content {
  // padding: 0 100px;
  // margin-bottom: $lg-offset;
  margin-bottom: 90px;
  // margin-bottom: 60px;


  &__img {
    grid-column: 5 span;
    display: flex;
    align-items:center;
    max-width: 530px;

    // img {
    //   width: 95%;
    // }
  }
  &__descr {
    grid-column: 7 span;
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: space-between;

    p {
      margin: 0;
      margin-bottom: $xs-offset;
      font-weight: 400;
      font-size: 20px;
      line-height: 170%;
      // line-height: 150%;
      color: inherit;

      &:last-of-type {
        margin-bottom: 30px;
      }
    }
  }
  &__btn {
    color: $dark-color;
    background-color: rgba($accent-color, 0.8);

    @include hover {
      color: $light-color;
      background-color: rgba($dark-color, 0.8);
      border-color: rgba($dark-color, 0.8);
    }
  }
}
