.product-table2 {
  &__wrapper {
    margin-bottom: 50px;
    color: #27292e;
  }

  &__title {
    margin: 0;
    margin-bottom: 8px;
    font-weight: 600;
    font-size: 20px;
    color: inherit;
  }

  &__table {
    padding: 30px;
    border-collapse: collapse;
    min-width: 100%;
      box-shadow:
      5px 5px 5px rgba(0,0,0,0.1),
      15px 15px 15px rgba(0,0,0,0.1),
      20px 20px 20px rgba(0,0,0,0.1),
      30px 30px 30px rgba(0,0,0,0.1),
      inset 1px 1px 2px #fff;

      thead {
        position: relative;
        width: 100%;

        tr {
          color: #555;
          background: linear-gradient(#dbdae1, #a3aaba);
        }

        &::before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 10px;
          height: 100%;
          background: linear-gradient(#fff, #fff, #e3e3e3);
          filter: blur(1px);
          z-index: 1;
        }

        &::after {
          content: '';
          position: absolute;
          top: 0;
          right: -1px;
          width: 10px;
          height: 100%;
          background: #9d9d9d;
          filter: blur(1px);
          z-index: 1;
        }

        th {
          font-weight: 500;
          font-size: 20px;
          color: $dark-color;
        }
      }

      tbody {
        tr {
          position: relative;
          margin-bottom: 2px;
          background-color: #fff;

          &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 10px;
            height: 100%;
            background: linear-gradient(#fff, #e3e3e3, #e3e3e3);
            filter: blur(1px);
            z-index: 1;
          }

          &::after {
            content: '';
            position: absolute;
            top: 0;
            right: -1px;
            width: 10px;
            height: 100%;
            background: #9d9d9d;
            filter: blur(1px);
            z-index: 1;
          }
        }
      }

      tr {
        display: grid;
        grid-template-columns: repeat(8, 1fr);
        border: 1px solid #ddd;
        padding: 20px;
        width: 100%;

        td, th {
          grid-column: 1 span;
          text-align: start;

          &:first-child {
            grid-column: 3 span;
          }
        }

        td {
          font-weight: 400;
          font-size: 18px;
          line-height: 144%;
          color: $dark-color;
        }
      }
  }
}

